import { useNavigate } from "react-router-dom";
import {isEmpty} from "lodash";

function Header(props){

  const navigate = useNavigate();
  const isLogin = !isEmpty(localStorage.getItem('x_access_token'));
  const logOut = () => {
    localStorage.removeItem('x_access_token');
    localStorage.removeItem('x_refresh_token');
  }

  return(
    <header>
      <nav>
        <button onClick={() => {navigate('/')}}></button>
        <button onClick={() => {navigate('/movieManage')}}></button>
        <button onClick={() => {navigate('/festival')}}></button>
        <button onClick={() => {navigate('/userManage')}}></button>
        <button></button>
        {isLogin ? <button className="logOutBtn" onClick={logOut}></button> : null}
      </nav> 
    </header>
  )
}

export default Header;