import axios from "axios";
import { useEffect, useState } from "react";
import List from "../../components/recyclingComponents/list";
import SearchFilter from "../../components/recyclingComponents/searchFilter";

function CopyMovieManage () {

  const [dataLength, setDataLength] = useState();
  const [queryData, setQueryData] = useState();
  const elements = {title: '', directors: '', scenarios: '', actors: '', summary: '', rating: '', genres: '', tags: ['', 'tagName'], date: ''}

  const listBtns = [
    {name: '', api: '/movie/delete'},
    {name: '', api: '/movie/open'},
    {name: '', api: '/movie/close'}
  ];

  const [tags, setTags] = useState();
  const [tagSelectMenus, setTagsSelectMenus] = useState([]);
  
  const [searchInputs, setSearchInputs] = useState({
    title: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'title', inputValue: ''},
    director: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'directors', inputValue: ''},
    scenario: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'scenarios', inputValue: ''},
    actor: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'actors', inputValue: ''},
    registrant: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'registrants', inputValue: ''},
    genre: {name: '', isPlural: true, isInput: false, type: 'select', addDataName: 'genres', inputValue: '', selectMenus: ['', '', '', '', '']},
    tag: {name: '', isPlural: true, isInput: false, type: 'select', addDataName: 'tags', inputValue: '', selectMenus: []},
    date: {name: '', isPlural: false, isInput: false, type:'date', addDataName: 'date', inputValue: ''}
  })

  const [addedSearchDatas, setAddedSearchDatas] = useState({
    title:[],
    directors:[],
    scenarios:[],
    actors:[],
    registrants:[],
    genres:[],
    tags:[],
    date:['', '']
  })

  const [searchOption, setSearchOption] = useState('and');
  

  useEffect(() => {
    axios.get('/api/tag/getTag')
    .then((res) => {
      setTags(res.data);
      var tagNames = [];
      res.data.map((tag) => {
        tagNames.push(tag.tagName);
      })
      setSearchInputs({...searchInputs, tag: {...searchInputs.tag, selectMenus: tagNames}})
    })
  },[])

  // useEffect(() => {
  //   axios.post('/api/movie/getLength', {queryData: queryData}, {"Content-Type": 'application/json'})
  //   .then((response) => {
  //     setDataLength(response.data);
  //   })
  // },[queryData])

  return (
    <div className="manage">
      <h2>  ()</h2>
      {/* <MovieSearchFilter setQueryData={setQueryData} /> */}
      <SearchFilter inputs={searchInputs} setInputs={setSearchInputs} addedDatas={addedSearchDatas} setAddedDatas={setAddedSearchDatas} queryData={queryData} setQueryData={setQueryData} searchOption={searchOption} setSearchOption={setSearchOption} setDataLength={setDataLength} getLengthApi={'movie/getLength'} />
      <List dataLength={dataLength} queryData={queryData} searchOption={searchOption} elements={elements} defaultShow={['title', 'directors', 'scenarios', 'tags']} listName='' getListApi={'copyMovie/getMovieList'} listBtns={listBtns} />
    </div>
  )
}

export default CopyMovieManage;