import axios from "../../api/config";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import apiUrl from "../../api/baseurl";


function FestivalMovieDetail() {
  const [movie, setMovie] = useState(true);
  const { id } = useParams();
  const elements = {
    videoInfo: {name: ' ', fields: {'mov_file': '', 'running_time': ' ', 'coloration_txt': ''}},
    movieInfo: {name: ' ', fields: {'mov_file_en': ' ', 'cat_name': '', 'gerne': ' ', 'rating_txt': '', 'nation': '', 'production_year': '', 'mov_plot': '', 'director_note': ' '}},
    producerInfo: {name: ' ', fields: {'director': ''}},
    remark: {name: '/', fields: {'remarks': '/'}},
  };

  useEffect(() => {
    axios.get(apiUrl+'/festival/'+id)
      .then((res) => {setMovie(res.data.rs[0])})
    
  }, [])
 
  return(
    <div className="festivalMovieDetail">
      {
        movie ?
        Object.entries(elements).map(([key, values]) => {
          return (
            <div>
              <div>{values.name}</div>
              {
                Object.entries(values.fields).map(([key, value]) => { 
                  return (
                    <div> 
                      <span>{value}</span>
                      <span>{movie[key]}</span> 
                    </div>
                  )
                })
              }  
            </div>
          );
        })
        :
        <div>Loading...</div> 
      }
    </div>
  )
}

export default FestivalMovieDetail;