import axios from "axios";
import { useEffect, useState } from "react";
import ManageMain from "../../components/recyclingComponents/manageMain";

function DeletedMovieManage () {

  const listName = ' ';

  const elements = {title: '', directors: '', screenwriters: '', actors: '', summary: '', rating: '', genres: '', tags: ['', 'tagName'], date: '', deletedDate: ''}

  const defaultShow = ['title', 'directors', 'screenwriters', 'tags'];
  const api = 'deletedMovie';

  const listBtns = [
    {name: '', api: '/deletedMovie/restore', prompt: { msg:"''  .", value: ''}},
    // {name: '', api: '/deletedMovie/restore' },
    {name: ' ', api: '/deletedMovie/delete', prompt: { msg: "''   .    .", value: '' }}
  ];
  
  const [searchInputs, setSearchInputs] = useState({
    title: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'title', inputValue: ''},
    director: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'directors', inputValue: ''},
    screenwriter: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'screenwriters', inputValue: ''},
    actor: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'actors', inputValue: ''},
    genre: {name: '', isPlural: true, isInput: false, type: 'select', addDataName: 'genres', inputValue: '', selectMenus: ['', '', '', '', '']},
    date: {name: '', isPlural: false, isInput: false, type:'date', addDataName: 'date', inputValue: ''},
    deletedDate: {name: '', isPlural: false, isInput: false, type:'date', addDataName: 'deletedDate', inputValue: ''},
    registrant: {name: '', isPlural: true, isInput: true, type: 'default', addDataName: 'registrants', inputValue: ''},
    tag: {name: '', isPlural: true, isInput: false, type: 'select', addDataName: 'tags', inputValue: '', selectMenus: []},
    isOpen: {name: '', isPlural: false, type:'isTrue', addDataName: 'isOpen', menus:['', '']}
  })

  const [addedSearchDatas, setAddedSearchDatas] = useState({
    title:[],
    directors:[],
    screenwriters:[],
    actors:[],
    registrants:[],
    genres:[],
    tags:[],
    date: ['', ''],
    deletedDate: ['', '']
  })
  
  useEffect(() => {
    axios.get('/api/tag/getTag')
    .then((res) => {
      var tagNames = [];
      for(let tag of res.data) {
        tagNames.push(tag.tagName);
      }
      setSearchInputs(inputs => ({...inputs, tag: {...inputs.tag, selectMenus: tagNames}}));
    })
  }, [])

  // useEffect(() => {
  //   axios.post('/api/movie/getLength', {queryData: queryData}, {"Content-Type": 'application/json'})
  //   .then((response) => {
  //     setDataLength(response.data);
  //   })
  // },[queryData])

  return (
    <div className="manage">
      <ManageMain searchInputs={searchInputs} setSearchInputs={setSearchInputs} addedSearchDatas={addedSearchDatas} setAddedSearchDatas={setAddedSearchDatas} api={api} elements={elements} defaultShow={defaultShow} listName={listName} listBtns={listBtns} />
    </div>
  )
}

export default DeletedMovieManage;