import { useState } from "react";
import { Route, Routes } from "react-router-dom";
import ManageMain from "../../components/recyclingComponents/manageMain";
import Upload from "../../components/recyclingComponents/upload";

function NoticeManage() {

  const listName = '';

  const elements = {noticeTitle: ' ', noticeContent: '', createdDate: ''};

  const defaultShow = ['noticeTitle', 'noticeContent', 'createdDate'];
  const api = 'notice';

  const searchInputs = {
    noticeTitle: {name: ' ', isPlural: true, isInput: true, type: 'default', addDataName: 'noticeTitle'},
    noticeContent: {name: ' ', isPlural: true, isInput: true, type: 'default', addDataName: 'noticeContent'},
    createdDate: {name: '', isPlural: false, isInput: false, type:'date', addDataName: 'createdDate'}
  };
  const [addedSearchDatas, setAddedSearchDatas] = useState({
    noticeTitle:[],
    noticeContent: [],
    createdDate: ['', '']
  });

  const uploadInputs = {
    noticeTitle: {name: ' ', isPlural: false, isInput: true, type: 'default', addDataName: 'noticeTitle'},
    noticeContent: {name: ' ', isPlural: false, isInput: false, type: 'textarea', addDataName: 'noticeContent'}
  };
  const [addedUploadDatas, setAddedUploadDatas] = useState({
    noticeTitle: '',
    noticeContent: ''
  });

  const listBtns = [
    {name: '', api: '/notice/delete' },
    {name: '', api: '/notice/open'},
    {name: '', api: '/notice/close'}
  ];


  return(
    <div className="manage">
      <h2>  </h2>
      <Routes>
        <Route path="/" element={<ManageMain searchInputs={searchInputs} addedSearchDatas={addedSearchDatas} setAddedSearchDatas={setAddedSearchDatas} api={api} elements={elements} defaultShow={defaultShow} listName={listName} uploadBtn={true} listBtns={listBtns} />} />
        <Route path="/upload" element={<Upload uploadInputs={uploadInputs} addedUploadDatas={addedUploadDatas} setAddedUploadDatas={setAddedUploadDatas} api={api} />} />
      </Routes>
    </div>
  )
}

export default NoticeManage;